@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts//Open_Sans//OpenSans-Regular.ttf");
}

@font-face {
  font-family: "Open Sans - Semibold";
  font-style: normal;
  font-weight: 600;
  src: url("../../fonts/Open_Sans//OpenSans-SemiBold.ttf");
}

$main-font-stack: "Open Sans", "Arial", sans-serif;
$main-font-size: 16px;
$main-lineheight: 24px;

$primary-color: #ef5b4c;
$secondary-color: #007bff;
$primary-background-color: #fff;
$secondary-background-color: #f9f9f3;
$primary-headers-color: #212529;
